import React from "react";

//component
import { Container, Row, Col, Image, Button, Badge } from "react-bootstrap";
import { isMobile } from "react-device-detect";
import { v4 as uuidv4 } from "uuid";
import dateFormat from "dateformat";

export default function CardsNews({ data, downloadButton, onSubmit }) {
  return (
    <>
      <Container fluid className={`my-5 m-0 p-0`}>
        <Row className={`w-100 m-0 p-0 h-100 justify-content-center`}>
          <Col
            lg={12}
            className={`news-box-shadow news-box-border ${
              isMobile ? "w-100" : "w-75"
            }`}
          >
            <Row className="m-0 p-0 w-100 border-animation">
              <Col lg={12} className={`${isMobile ? "order-1" : ""}`}>
                <h1 className="news-header-title-text pt-3">
                  {data?.content_title}
                </h1>
                <Badge bg="primary" pill>
                  {dateFormat(data?.created_at, "mmmm dd, yyyy")}
                </Badge>
                {data?.content_tags?.map((tags) => {
                  return (
                    <Badge
                      className="pill-cursor mx-1"
                      bg="primary"
                      pill
                      key={uuidv4()}
                      onClick={() => onSubmit({ Search: tags })}
                    >
                      {tags}
                    </Badge>
                  );
                })}
              </Col>
              <Col
                lg={8}
                id="news-description-container"
                className={`${isMobile ? "order-4" : ""}`}
                dangerouslySetInnerHTML={{
                  __html: data?.content_description?.content_description,
                }}
              ></Col>
              <Col lg={4} className={`${isMobile ? "order-2" : ""}`}>
                <Image
                  className="news-image"
                  src={
                    process.env.REACT_APP_API_URL +
                    "/images/contents/" +
                    data?.content_thumbnail
                  }
                />
              </Col>
              <Col
                lg={12}
                className={`${isMobile ? "order-3 text-center" : ""} p-3`}
              >
                <Button
                  variant="primary"
                  className="news-download-button"
                  onClick={() => downloadButton(data?.content_files)}
                >
                  Download Newsletter
                </Button>
              </Col>
            </Row>
          </Col>
        </Row>
      </Container>
    </>
  );
}
